body {
  margin: 0;
  /* Remove the default margin */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
Body {
  background-color: #15181a;
  /* 0c0c0c */
  color: white;
  font-family: 'Roboto', sans-serif;
}

.app-frame {
  position: fixed;
  padding: 0;
  margin: 1em;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  border-color: white;
  border-style: solid;
  border-width: 0.01em;
}

.App {
  text-align: center;
}

.app-header {
  z-index: 3;
}

.App-link {
  color: #61dafb;
}

@font-face {
  font-family: HelveticaNow;
  src: url(/static/media/HelveticaNow.96976e7e.ttf);
}

@font-face {
  font-family: NeueMontreal;
  src: url(/static/media/NeueMontreal-Bold.821f2668.otf);
}


.app-content {
  position: fixed;
  bottom: 0;
  right: 0;
  text-align: right;
  margin: 2em;
  font-family: "NeueMontreal", sans-serif;
  z-index: 1;
  text-decoration: none;
}

a {
  color: white;
  text-decoration: none;
  transition: background-color 0.5s;
  transition: color 0.7s;
}

a:hover {
  /* Look as selected */
  /* background-color: #ffffff; */
  color: #15181a;
  background-color: #ffffff
}

.particles-bg {
  width: 100%;
  height: 99%;
  bottom: 1em;
  top: 0.2em;
  position: absolute;
  z-index: -1;
  opacity: 0.10;
}
Body { 
  color: white;
}

/* Change color when selecting text */
::selection {
    background-color: #ffffff;
    color: #50549a;
  }

  
