.project-box { 
    width: 750px; 
    height: 250px;

    background-color: rgba(255, 255, 255, 0.1);
    
    border-radius: 1px;
    border-color: white;
    border-style: solid;
    border-width: 0.01em;


    color: white;
    text-align: left;

    margin-top: 3em;
    margin-left: 12em;
}

@font-face {
    font-family: HelveticaNow;
    src:url("/src/font/HelveticaNow.ttf");
  }

@font-face {
font-family: NeueMontreal;
src:url("/src/font/NeueMontreal-Bold.otf" );
}

.project-box-image { 
    width: 250px; 
    height: 250px;
    float: left;

}

.project-box-img { 
    width: 250px; 
    height: 250px;
    float: left;
    background-size: cover;
    /* Maintain image in div*/
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.project-box-content { 
    margin-left: 16.5em; 
    margin-right: 2em;
}
.project-box-title {
    width: 500px; 
    height: 50px; 
    
    position: relative;
    margin-top: 0.4em;
    margin-bottom: 1em;


    font-family: 'NeueMontreal', sans-serif;
    font-size: 1.75em;
    font-weight: bold;

}

.project-box-description {
    font-size: 1em;
    
    position: relative; 
    margin-top: 0.2em;

    font-family: 'Roboto', sans-serif;
}

.project-box-date {
    position: relative; 
    /* text justify  */
    text-align: justify;
    margin-top: 1em;

    font-style: italic;
}

.project-box-link { 
    margin-top: 1em; 
    /* Bold */
    font-family: "NeueMontreal", sans-serif;
    font-weight: 100;
}
@font-face {
    font-family: NeueMontreal;
    src:url("/src/font/NeueMontreal-Bold.otf" );
  }

.app-projects {
    border-bottom: 1px solid #ccc;
    overflow-y: auto;
    overflow-x:hidden;
    max-height: 100vh;
    scrollbar-width: none;
    scroll-behavior: smooth;
}

.app-projects::-webkit-scrollbar {
    display: none;
}
.app-projects-title { 
    font-family: 'NeueMontreal', sans-serif;
    font-weight: 900;
    font-size: 4.2em;
    z-index: 0;
    background:transparent;

    position: relative;
    margin-left: 0.25em;
    text-align: left;

    margin-bottom: 0.4em;
}

.app-projects-content-box {
    display: flex;
    flex-wrap: wrap;
    height: 48.5em; 
}
